<style scoped>

</style>
<template>
  <div>
    <Tabs v-model="type">
      <TabPane label="按工位租凭" name="0"></TabPane>
      <TabPane label="按面积租凭" name="1"></TabPane>
    </Tabs>
    <Divider orientation="left">列表筛选</Divider>
    <Row class="search">
      <Form inline :model="formData" :label-width="80">
        <FormItem label="工位ID">
          <Input v-model="formData.id" placeholder="请输入工位id" class="filter-input"></Input>
        </FormItem>
        <FormItem label="工位编号">
          <Input v-model="formData.workplace_no" placeholder="请输入工位编号" class="filter-input"></Input>
        </FormItem>
      </Form>
    </Row>
    <div class="filter-btn">
      <span class="span">操作 </span>
      <Button style="font-weight: bold" type="info" icon="ios-search" @click="loadData(1)"> 搜索</Button>
      <Button style="font-weight: bold" type="warning" icon="md-refresh" @click="ref()">重置</Button>
      <Button style="font-weight: bold" type="success" icon="md-add" @click="add()">新建</Button>
    </div>
    <Divider orientation="left">列表数据</Divider>
    <Table stripe border :columns="columns" :data="listData.data">
    </Table>
    <Spin size="large" fix v-if="load"></Spin>
    <Row v-if="listData.last_page > 1" style="text-align: center;margin-top: 20px">
      <Page @on-change="loadData" :total="listData.total" :page-size="listData.per_page"
            :current="listData.current_page" size="small"/>
    </Row>
  </div>
</template>

<script>
import api from './api'

export default {
  data () {
    return {
      type: '0',
      formData: {},
      load: false,
      listData: {},
      columns: [
        {
          title: 'id',
          align: 'center',
          key: 'id'
        },
        {
          title: '工位编号',
          align: 'center',
          key: 'workplace_no'
        },
        {
          title: '分组',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.workplace_group ? params.row.workplace_group.name : '')
          }
        },
        {
          title: '所属空间',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.space.name + '-' + params.row.space.address)
          }
        },
        {
          title: '租赁单价',
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('span', '￥' + params.row.price),
              h('span', this.type === '0' ? '/个' : '/㎡')
            ])
          }
        },
        {
          title: '租赁团队',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.enter_team ? params.row.enter_team.company : '-')
          }
        },
        {
          title: '状态',
          key: 'status',
          align: 'center',
          render: (h, params) => {
            return h('span', params.row.status === 0 ? '未出租' : '已出租')
          }
        },
        {
          title: '到期时间',
          key: 'deadline',
          align: 'center'
        },
        {
          title: 'Action',
          width: 200,
          align: 'center',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'success',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.add(params.row.id)
                  }
                }
              }, '编辑'),
              h('Button', {
                props: {
                  type: 'error',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.del(params.row)
                  }
                }
              }, '删除')
            ])
          }
        }
      ]
    }
  },
  activated () {
    this.loadData()
  },
  methods: {
    loadData (page) {
      if (page) {
        this.formData.jump_page = page
      }
      this.formData.type = this.type
      this.load = true
      api.index(this.formData, res => {
        this.listData = res.data.data
        this.load = false
      }, () => {
        this.load = false
      })
    },
    add (id) {
      this.$router.push({
        name: 'workplaceEdit',
        query: {
          id: id
        }
      })
    },
    ref () {
      this.formData = {}
      this.loadData(1)
    },
    del (item) {
      this.$Modal.confirm({
        title: '确定删除' + item.workplace_no + '吗?',
        okText: '删除',
        cancelText: '取消',
        loading: true,
        onOk: () => {
          api.delete({ id: item.id }, () => {
            this.$Modal.remove()
            this.$Message.success('删除成功')
            this.loadData()
          },
          () => {
            this.$Modal.remove()
          })
        }
      })
    }
  },
  watch: {
    type () {
      this.loadData(1)
    }
  }
}
</script>
